.we {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #fff9ea;
}
.wePages {
  display: block;
  size: 100%;
  box: horizontal top space-between;
}
.wePage {
  flex: 0 1 auto;
  position: relative;
  width: 33.33%;
  align-self: stretch;
  box: horizontal middle center;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.wePage a {
  display: block;
  position: relative;
  height: 60px;
  border: solid 1px #fff;
  width: 80%;
  text-align: center;
  line-height: 60px;
  color: #fff;
  font-family: 'Raleway', sans-serif;
  font-size: 28px;
  font-weight: 300;
  letter-spacing: 0.08em;
  text-decoration: none;
}
.wePage a:after {
  display: block;
  position: absolute;
  content: '';
  width: 100%;
  height: 60px;
  border: solid 1px #fff;
  top: -5px;
  left: -5px;
}
.wePageGradient {
  display: block;
  position: absolute;
  size: 100%;
  opacity: 0.36;
  background-image: linear-gradient(to top, rgba(0,0,0,0), #000);
}
