.header {
  display: block;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 9000;
  box: horizontal middle space-between;
  padding: 60px 130px 0 130px;
  overflow: hidden;
}
.headerLeft {
  box: horizontal middle;
}
.headerMenu {
  box: horizontal middle right;
}
.headerLogo {
  flex: 0 1 auto;
  margin-right: 10px;
  size: 262px 46px;
  margin-top: -8px;
  background-image: url("../img/logo.png");
  background-repeat: no-repeat;
}
.headerControl {
  box: horizontal middle;
  transition: all 0.4s ease;
  transform: translateY(-200px);
}
.headerControlIsShow {
  transform: translateY(0);
}
.buttonBack {
  flex: 0 1 auto;
  background-color: transparent;
  border: 0;
  outline: 0;
  margin-right: 50px;
  cursor: pointer;
}
.buttonBack svg {
  size: 30px;
  stroke: #fff;
  fill: #fff;
}
.headerMenuActivateCart {
  flex: 0 1 auto;
  margin-right: 55px;
}
.buttonActivateCart {
  display: inline-block;
  background-color: transparent;
  border: 0;
  outline: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.29;
  letter-spacing: 0.15em;
  color: #fff;
  text-decoration: underline;
  cursor: pointer;
}
.headerMenuSocialItems {
  flex: 0 1 auto;
  margin-right: 32px;
  box: horizontal middle;
}
.headerMenuSocialItem {
  flex: 0 1 auto;
  margin-right: 16px;
}
.headerMenuSocialItem a svg {
  size: 24px;
  fill: #fff;
}
.headerMenuSocialItem:last-child {
  margin-right: 0;
}
.headerMenuLang {
  flex: 0 1 auto;
  margin-right: 32px;
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.29;
  letter-spacing: 0.15em;
  color: #fff;
  cursor: pointer;
}
.headerMenuShowNav {
  flex: 0 1 auto;
}
.buttonShowNav {
  background-color: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
}
.buttonShowNav svg {
  size: 40px;
  stroke: #fff;
}
.headerBlack * {
  color: #4a4a4a;
}
.headerBlack * svg {
  fill: #4a4a4a;
}
.headerBlack .buttonBack svg {
  stroke: #4a4a4a;
}
.headerBlack .headerMenuSocialItem a svg {
  fill: #4a4a4a;
}
.headerBlack .buttonShowNav svg {
  stroke: #4a4a4a;
}
.headerBlack .headerLogo {
  background-image: url("../img/logoBlack.png");
}
