.weTeam {
  display: block;
  background-color: #fff9ea;
  box: vertical center;
}
.weTeamHeader {
  display: block;
  margin-bottom: 30px;
}
.weTeamHeader h2 {
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: 300;
  letter-spacing: 0.16em;
  color: #4a4a4a;
  text-align: center;
  margin-bottom: 10px;
}
.weTeamHeader h3 {
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0.15em;
  color: #4a4a4a;
  text-align: center;
  margin-bottom: 15px;
}
.weTeamPersons {
  display: block;
  max-width: 900px;
  margin: 0 atuo;
  box: horizontal top space-between;
}
.weTeamPersonsColumn {
  lost-column: 1/2 2 100px flex;
}
.weTeamPerson {
  display: block;
  box: vertical top left;
  margin-bottom: 40px;
}
.weTeamPerson img {
  width: 100%;
  margin-bottom: 10px;
}
.weTeamPersonName {
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  font-weight: 600;
  color: #4a4a4a;
  margin-bottom: 7px;
}
.weTeamPersonPosition {
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.12em;
  color: #4a4a4a;
  margin-bottom: 10px;
}
.weTeamPersonSlogan {
  display: block;
  position: relative;
  margin-top: 10px;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 25px;
  color: #4a4a4a;
  margin-bottom: 10px;
}
.weTeamPersonSlogan:after {
  display: block;
  position: absolute;
  content: '';
  size: 42px 1px;
  background-color: #4a4a4a;
  top: -10px;
}
