.restaurantPageGallery {
  display: block;
  position: absolute;
  transform: translateY(101vh);
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #f00;
  transition: all 0.4s ease;
  z-index: 5000;
}
.restaurantPageGalleryShow {
  transform: translateY(0vh);
}
.restaurantPageGallerySlider {
  size: 100%;
}
.restaurantPageGallerySlider .swiper-slide {
  size: 100%;
  position: relative;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.restaurantPageGallerySlider .swiper-slide img {
  width: 100%;
}
.restaurantPageGallerySlider .swiper-button-prev {
  display: block;
  position: absolute;
  top: calc(50% - 36px);
  left: 40px;
  z-index: 5001;
  cursor: pointer;
}
.restaurantPageGallerySlider .swiper-button-prev svg {
  size: 25px 72px;
  stroke: #fff;
}
.restaurantPageGallerySlider .swiper-button-next {
  display: block;
  position: absolute;
  top: calc(50% - 36px);
  right: 40px;
  z-index: 5001;
  cursor: pointer;
}
.restaurantPageGallerySlider .swiper-button-next svg {
  size: 25px 72px;
  stroke: #fff;
}
.restaurantPageGalleryScroll {
  position: absolute;
  bottom: 20px;
  left: center;
  z-index: 5000;
}
.restaurantPageGalleryScroll svg {
  size: 22px 30px;
  stroke: #fff;
}
.restaurantSlideContent {
  position: absolute;
  top: center;
  padding-left: 169px;
}
.restaurantSlideContent h2 {
  display: block;
  position: relative;
  font-family: 'Raleway', sans-serif;
  font-size: 28px;
  font-weight: 300;
  letter-spacing: 0.08em;
  margin-bottom: 28px;
  color: #fff;
}
.restaurantSlideContent h2:after {
  display: block;
  position: absolute;
  content: ' ';
  size: 104px 2px;
  background-color: #fff;
  bottom: -18px;
}
.restaurantSlideContent p {
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.15em;
  color: #fff;
  max-width: 450px;
}
