.buttonOutline {
  background-color: transparent;
  height: 35px;
  border: 1px solid #fff;
  outline: 0;
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 19px;
  letter-spacing: 0.15em;
  color: #fff;
  padding: 8px 12px;
  cursor: pointer;
}
.buttonOutline svg {
  size: 25px 9px;
  stroke: #fff;
}
