.franchising {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #fff9ea;
  box: horizontal top;
}
.franchisingVideo {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  height: 100%;
  width: 100%;
  background-color: #000;
}
.franchisingVideoCover {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../img/franchising_cover.jpg") no-repeat;
  background-size: cover;
  background-position: center;
}
.franchisingScroll {
  position: absolute;
  bottom: 20px;
  left: center;
  z-index: 5000;
}
.franchisingScroll svg {
  size: 22px 30px;
  stroke: #fff;
}
.franchisingScrollBlack {
  position: absolute;
  bottom: 20px;
  left: center;
  z-index: 6000;
}
.franchisingScrollBlack svg {
  size: 30px;
  stroke: #000;
}
