.franchisingPage {
  display: block;
  position: absolute;
  transform: translateY(101vh);
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #fff9ea;
  transition: all 0.4s ease;
  z-index: 1000;
  padding-top: 250px;
  box: horizontal top center;
}
.franchisingPageShow {
  transform: translateY(0vh);
}
.franchisingPageWrap {
  box: horizontal top;
  max-width: 1400px;
  padding: 0 50px;
}
.franchisingPageColumn {
  lost-column: 1/3 3 80px flex;
  position: relative;
}
.franchisingPageColumn:after {
  display: block;
  position: absolute;
  top: 200px;
  right: -40px;
  content: '';
  width: 1px;
  height: calc(100% - 200px);
  background-color: #979797;
}
.franchisingPageColumn:last-child:after {
  display: none;
}
.franchisingImage {
  box: horizontal top center;
  height: 200px;
}
.franchisingContent h2 {
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  font-weight: 300;
  letter-spacing: 0.14em;
  color: #4a4a4a;
  text-align: center;
  margin-bottom: 36px;
}
.franchisingContent p {
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 200;
  letter-spacing: 0.11em;
  text-align: justify;
  color: #4a4a4a;
}
